import {
    LANGS
} from 'i18n/lang';
import Vue from 'vue';
export default {
    name: 'head-nav',
    data() {
        return {
            changePassDialog: {
                show: false,
                isModal: true,
                default_value: {},
                labelWidth: "180px",
                rules: {
                    ori_password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    new_password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }, {
                        trigger: 'blur',
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error(''));
                            } else {
                                if ('' !== this.changePassDialog.default_value.new_password) {
                                    this.$refs["changePass-form"].$refs["form-data"].validateField('confirm_password');
                                }
                                callback();
                            }
                        }
                    }],
                    confirm_password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }, {
                        trigger: 'blur',
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error(''));
                            } else if (value !== this.changePassDialog.default_value.new_password) {
                                callback(new Error('!'));
                            } else {
                                callback();
                            }
                        }
                    }],
                }
            },
            locale: this.$store.state.global.locale,
            langs: LANGS
        }
    },
    mounted() {
        // this.setDialogInfo('access');
        // this.onGetSetting();
    },
    methods: {
        /**
         * 
         */
        logout() {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$store.dispatch('remove_userinfo').then(() => {
                    this.$router.push('/login');
                });
            });
        },

        /**
         * 
         * @param  {object} userinfo 
         */
        updUserPass(userinfo) {
            this.$refs[userinfo].validate((valid) => {
                if (valid) {
                    this.$$api_common_ajax({
                        data: {
                            old_password: this.dialog[userinfo].old_password,
                            password: this.dialog[userinfo].password,
                            password_confirm: this.dialog[userinfo].password_confirm
                        },
                        fn: data => {
                            this.dialog.show_pass = false;
                            this.$message.success('');
                        }
                    });
                }
            });
        },

        /**
         * 
         * @Author Dannis
         * @param  {[type]} command [description]
         * @return {[type]}         [description]
         */
        handleCommand(command) {
            this.$store.dispatch('update_global_locale', command).then(() => {
                this.locale = command;
                Vue.config.lang = command;
            });

        }
    }
}


